html{
    background-color: #f6f9fb;
}

a{
    color: #1a66b3;
}
.register_head{
    width: 960px;
    margin: 0 auto;
    padding-top: 2px;
    height: 71px;
    color: #404040;
    border-bottom: 3px solid #ec7814;
    background: #fff url(https://login.dangdang.com/images/unite_header_bg_1101.gif) repeat-x 0 -148px;
    .logo{
        float: left;
        width: 172px;
        height: 71px;
        overflow: hidden;
    }
    .nav{
        width: 500px;
        height: 21px;
        float: right;
        .line{
            padding: 0 5px;
        }
        p{
           
            text-align: right;
            margin-top: 5px;
            margin-right: 50px;
        }
        a:hover{
            text-decoration: underline;
        }
    }
}

#bd{
    margin: 0 auto;
    width: 960px;
    padding-top: 40px;
}
.shadow_box{
    background-color: #ebeeef;
    width: 950px;
    margin-bottom: 40px;
    position: relative;
    .register_box{
        background: #fff;
        position: relative;
        top: -10px;
        right: -10px;
        width: 950px;
        box-shadow: -1px 1px 1px rgb(232,235,236);
        
    }
    .head{
        height: 74px;
        border-bottom: 1px solid  #eceef2;
        padding-right: 15px;
        margin: 0 10px;
        .more{
            float: right;
            color: #646464;
            line-height: 22px;
            margin-top: 40px;
            .teshu{
                padding-left: 16px;
                margin-right: 15px;
                background: url(../img/icon_20150706.png) 0 2px no-repeat;
            }
        }
        .head_aa{
            border-bottom: none;
            width: 135px;
            height: 46px;
            background-color: #fff;
            display: inline-block;
            position: relative;
            left: 20px;
            border: 1px solid #eceef2;
            top: 27px;
            font-size: 16px;
            line-height: 42px;
            text-align: center;
            color: #464646;
            margin-left: 6px;
        }
        a{
            color: #646464;
        }
    }

    .body{
        padding: 45px 0 25px;
        position: relative;
        margin-bottom: 20px;
        .new_tip{
            line-height: 24px;
            color: #828282;
            padding-left: 22px;
            background: url(https://login.dangdang.com/images/new_tip.png) 3px 5px no-repeat #fef7f5;
            width: 670px;
            border: 1px solid #fedbd1;
            margin: -30px auto 30px;
        }
        .main_con{
            width: 500px;
            margin: 0 auto;
            span{
                font-size: 14px;
                color: #646464;
                margin-right: 5px;
            }
            span.isok{
                display: inline-block;
                margin-top: 14px;
                width: 16px;
                height: 16px;
                background: url('https://login.dangdang.com/images/icon_20150706.png') 0 -34px no-repeat;
            }
            input:not(#checkbox){
                border: 1px solid #e6e6e6;
                height: 18px;
                width: 290px;
                margin-right: 10px;
                padding: 9px 10px;
                line-height: 18px;
                outline: 0 none;
                border-radius: 2px;
                color: #333;
            }
            // .wrong{
            //     border-color: #ff4646 !important;
            //     background-color: #fef0ef;
            //     color: #f46561;
            // }
            div{
                padding-bottom: 30px;
                position: relative;
            }
            i{
                display: block;
                font-style: normal;
                line-height: 14px;
                padding: 0 4px;
                margin-right: 5px;
                position: absolute;
                left: 80px;
                bottom: 8px;
                color: #787878;
            }
            .again{
                line-height: 40px;
            }
            .btn{
                border: none;
                display: inline-block;
                font-size: 20px;
                border: 0;
                width: 178px;
                height: 44px;
                line-height: 44px;
                cursor: pointer;
                text-align: center;
                background-color: #ff2832;
                color: #fff;
                border-radius: 22px;
                margin-left: 116px;
                padding-bottom: 0;
                margin-bottom: 20px;
            }
        }
    }
}
.again a:hover{
    text-decoration: underline;
}

@import "dlogin_footer";